.page {
  
 
  margin: 0 auto;
  &:nth-child(1) {
    &.move-enter {



      .absolute-center {
        opacity: 0;
        z-index: 1;
        transform: translateX(-50%) translateY(-1000%);  
    
      }
    
      &.move-enter-active {
        .absolute-center {
    
          opacity: 1;
          transform: translateX(-50%) translateY(-50%);
      
          transition: all 1000ms ease-in-out;
      
        }
      }
    }

    .move-exit {

      .absolute-center {
    
        opacity: 1;
        z-index: 1;
        transform: translateX(-50%) translateY(-50%);
    
      }
    
      &.move-exit-active {
    
        .absolute-center {
    
          opacity: 0;
          transform: translateX(-50%) translateY(-1000%);
      
          transition: all 400ms ease-in-out;
        }
      }
    }

  }
}

.page {
  &:nth-child(2) {
    &.move-enter {



      .absolute-center {
        opacity: 0;
        z-index: 1;
        transform: translateX(-50%) translateY(1000%);  
    
      }
    
      &.move-enter-active {
        .absolute-center {
    
          opacity: 1;
          transform: translateX(-50%) translateY(-50%);
      
          transition: all 1000ms ease-in-out;
      
        }
      }
    }

    .move-exit {

      .absolute-center {
    
        opacity: 1;
        z-index: 1;
        transform: translateX(-50%) translateY(-50%);
    
      }
    
      &.move-exit-active {
    
        .absolute-center {
    
          opacity: 0;
          transform: translateX(-50%) translateY(1000%);
      
          transition: all 400ms ease-in-out;
        }
      }
    }
  }
}  

.slides {
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.slide {
  text-align: center;
}

.fp-slidesContainer {
  //width: 100%!important;
}


.absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  max-width: 800px;
  width: 90%;
}

.home {
  text-align: center;
  color: white;
}

.about {
  text-align: center;
  color: white;
}

ul.nav {
  margin: 0px;
  padding: 10px;
  width: 100%;
  text-align: right;
  background: rgba(255,255,255,0.8);
  li {
    display: inline;
    a {
      color: black;
      padding: 10px;
      text-decoration: none;
      text-transform: uppercase;
      &:hover {
        background: black;
        color: white;
      }
    }
  }
}

canvas {
	width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  opacity: 0.8;
  z-index: -1;
  animation: fade-in-80 2s ease-in;

  // only enabled now for production reasons
  //display: none;
}


.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 20vmin;
  cursor: pointer;
}
/* new class here */
.App-logo-paused {
  animation-play-state: paused;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes fade-in-80 {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.8;
  }
}

@keyframes text-shadow {
  from {
    text-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2);
    transform: translateX(0.2vw);
  }
  to {
    text-shadow: -2px 1px 2px rgba(0, 0, 0, 0.11);
    transform: translateX(0vw);
  }
}

@keyframes expand {
  from {
    outline: 0px solid #f2efe8;
    width: 0px;
  }
  to {
    outline: 1px solid #f2efe8;
    width: 200px;
  }
}